import React from "react";
import { graphql } from "gatsby";
import ArticleList from "../components/articleList";
import Layout from "../components/layout";

export const query = graphql`
  query Tag($slug: String!) {
    tag: strapiTag(slug: { eq: $slug }) {
      tagName
    }

    articles: allStrapiArticle(
      filter: { status: { eq: "published" }, tags: { elemMatch:{ slug: { eq: $slug } } } }
      sort:{fields:publishedAt ,order:DESC}
    ) {
      edges {
        node {
          slug
          title
          tags{
            tagName
            slug
          }
          category {
            name
          }
          image {
            childImageSharp {
              fixed(width: 660) {
                src
              }
              fluid{
                src
              }
            }
          }
          author {
            name
            picture {
              childImageSharp {
                fixed(width: 30, height: 30) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
        }
      }
    }
  }
`;

const Tag = ({ data }) => {
  const articles = data.articles.edges;
  const tagName = data.tag.tagName;
  const seo = {
    metaTitle: tagName,
    metaDescription: `All ${tagName} articles`,
  };

  return (
    <Layout seo={seo}>
          <ArticleList articles={articles} topSubTitle={`標籤 - [#${tagName}]`}/>
    </Layout>
  );
};

export default Tag;